<template>
    <div>
      <mt-field label="用户名" placeholder="请输入用户名" v-model="username"></mt-field>
      <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
      <mt-field label="QQ" placeholder="请输入QQ" type="text" v-model="number"></mt-field>
      <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>

      <mt-radio
        title='选择申请试听课程'
        v-model="course"
        :options="['H5前端开发', 'Java', 'UI设计']">
      </mt-radio>

      <div class="listen" @click="sub">申请试听</div>
    </div>
</template>
<style>

</style>
<script>
  import { MessageBox } from 'mint-ui';
    export default{
      data(){
        return{
          username:'',
          phone:'',
          number:'',
          password:'',
          course:'',
        }
      },
      methods:{
        sub(){
            if(this.username && this.phone && this.number && this.password && this.course){
              MessageBox.alert('恭喜申请成功!')
            }else {
              MessageBox.alert('请输入正确信息!')
            }
        }
      }
    }
</script>



