<template>
  <div>
    <h4>请输入注册信息</h4>
    <mt-field label="手机号" placeholder="请输入手机号" type="tel" v-model="phone"></mt-field>
    <mt-field label="密码" placeholder="请输入密码" type="password" v-model="password"></mt-field>
    <mt-field label="确认密码" placeholder="请确认密码" type="password" v-model="pswd"></mt-field>
    <div @click="login" class="reg">注册</div>
  </div>
</template>
<style>

</style>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        phone:'',
        password:'',
        pswd:''
      }},
    methods:{
      login(){
        if( !this.phone && !this.password && !this.pswd){
          MessageBox.alert('请将信息填写完整！')
        }else if(this.password!=this.pswd ){
          MessageBox.alert('两次密码不匹配！')
        }else  if(this.phone!=11){
          MessageBox.alert('手机号必须为11位！')
        }else{
          this.$store.state.people[this.phone] = this.password
          MessageBox.alert('注册成功！即将返回登陆页面')
          this.$router.push('/login')
        }
      }
    }
  }
</script>



