<template>
  <div>
    <div class="nuser">
      <div class="icon user">手机号：</div>
      <mt-field placeholder="手机号" v-model="phone" class="line"></mt-field>
    </div>
    <div class="npass">
      <div class="icon pasw">密 码 ：</div>
      <mt-field  placeholder="密码" type="password" v-model="password" class="line"></mt-field>
    </div>
    <div class="forget">忘记密码?</div>
    <div class="login" @click="login">登录</div>
    <div class="none"><router-link :to="{path:'/Register'}">没有账号？免费注册</router-link></div>
    <div class="third">第三方登录：
      <span></span>
      <span></span>
      <span></span>
    </div>
  </div>
</template>
<style>

</style>
<script>
  import { MessageBox } from 'mint-ui';
  export default{
    data(){
      return{
        phone:'',
        password:''
      }
    },
    methods:{
      login(){
        if(this.$store.state.people[this.phone] == this.password){
          MessageBox.alert('登录成功!欢迎来到个人中心');
          this.$router.push('/about')
        }else {
          MessageBox.alert('信息不正确或你还没有注册，请注册!');
          this.phone = '';
          this.password = ''
        }
      }
    }
  }
</script>



