export default ({
  state:{
    show:'小熙',
    num:'true'
  },
  mutations:{
    switch_dialog(state){
      if(state.show == '小熙'){
        state.show='This is me'
      }else {
        state.show = '小熙'
      }
    }
    },
  action:{
    list(state){
      if(1<0){
        state.commit('switch_dialog')
      }
    }
  },
  getters:{
    not_show(state){
      return ! state.num;
    }
  }
})




